/* App.css */

.container {
    padding-top: 20px;
  }
  
  textarea {
    resize: vertical;
    transition: all 0.2s ease-in-out;
  }
  
  /* Responsive tweaks */
  @media (max-width: 576px) {
    .btn {
      width: 100%;
      margin: 5px 0;
    }
  
    .d-flex > div {
      height: 25px !important;
      width: 25px !important;
    }
  }
  
  .accordion-button::after{
    filter:invert(1);
  }